<template>
  <div class="layout">
    <Layout>
      <Header>
      </Header>
      <Content :style="{padding: '0 50px'}">
        <Card>
          <div style="min-height: 800px;">
<!--            <div class="Tag_box">-->
<!--              <Tag checkable color="primary">结婚</Tag>-->
<!--              <Tag checkable color="success">婚礼</Tag>-->
<!--              <Tag checkable color="error">四大金刚</Tag>-->
<!--              <Tag checkable color="warning">婚纱摄影</Tag>-->
<!--              <Tag checkable color="default">珠宝</Tag>-->
<!--              <Tag checkable color="warning">蜜月</Tag>-->
<!--            </div>-->
            <div>
              <Input v-model="search_model" search enter-button="搜索" placeholder="在此处输入进行搜索..." style="width: 400px;float: left;margin: auto" @on-search="search" />
              <Button type="info" @click="jump_collect()" style="float: right">周报收藏</Button>
              <div class="clearfloat"></div>
            </div>
            <div class="Table_data" style="margin-top: 20px">
              <Table no-data-text="稍等数据正在获取" windth="800" height="auto" border :columns="YQcolumns" :data="YQdata" ref="table"></Table>
            </div>
            <template>
              <Page ref="pages" :total="pageTotal" :current="page_now" prev-text="Previous" next-text="Next" style="text-align: center;margin-top: 20px" @on-change="changePage" />
            </template>
          </div>
        </Card>
      </Content>
      <Footer class="layout-footer-center">BIG_DATA 舆情搜索</Footer>
    </Layout>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data (){
      return{
        search_model : '',
        YQcolumns: [
          {
            title: 'id',
            // align: 'center',
            key: 'id',
            width: 70,
          },
          {
            title: '标题',
            // align: 'center',
            key: 'title',
          },
          {
            title: '概要',
            // align: 'center',
            key: 'digest',
          },
          {
            title: '来源',
            align: 'center',
            key: 'source',
            width: 100,
          },
          {
            title: '发布时间',
            align: 'center',
            key: 'report_time',
            width:100,
          },
          {
            title: '操作',
            key:'action',
            align: 'center',
            width: 200,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'success',
                    size: 'small'
                  },
                  style: {
                    marginLeft: '2px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                },'去原网站看看'),
                h('Button', {
                  props: {
                    type: 'warning',
                    size: 'small'
                  },
                  style: {
                    marginLeft: '5px'
                  },
                  on: {
                    click: () => {
                      this.collect(params.index)
                      // this.time(params.index)
                    }
                  }
                },'收藏添加'),
              ]);
            }
          },
        ],
        YQdata: [],
        pageTotal: 0,
        page_now: 1,
      }
    },
    methods :{
      getData() {
        let that = this;
        const path = 'http://148.70.148.229:8003/api/page/';
        axios.get(path).then(function (response) {
          let msg = response.data.data;
          that.YQdata = msg;
          that.pageTotal = response.data.pageNum
        }
        )
      },
      show (index) {
        window.open(this.YQdata[index].url, '_blank');
      },
      collect(index){
        this.time();
        let report_id = this.YQdata[index].id;
        const path = 'http://148.70.148.229:8003/api/update/';
        axios.get(path, {params:{edit_id:report_id}}).then(function (response) {
        });
      },
      time () {
        this.$Notice.open({
          title: '收藏添加完毕',
          desc: '已经将本文章添加至本日收藏',
          duration: 2
        });
      },
      changePage(index) {
        let that = this;
        const path = 'http://148.70.148.229:8003/api/page/';
        axios.get(path, {params:{keyword:that.search_model,pageNum:index}}).then(function (response) {
            let msg = response.data.data;
            that.YQdata = msg;

          }
        )
      },
      search() {
        this.$refs['pages'].currentPage = 1;
        let that = this;
        const path = 'http://148.70.148.229:8003/api/page/';
        axios.get(path, {params:{keyword:that.search_model}}).then(function (response) {
          let msg = response.data.data;
          that.YQdata = msg;
          that.pageTotal = response.data.pageNum
          }
        )
      },
      jump_collect () {
        let collect = this.$router.resolve({path:'YQ/report_list'});
        window.open(collect.href, '_blank')
      },
    },
    created() {
      this.getData()
    }
  }
</script>
<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }

  .layout-footer-center{
    text-align: center;
  }

  .clearfloat{
    clear:both
  }
</style>



