<template>
  <Card style="width:800px;margin: 0 auto">
    <p class="report_title">
      舆情周报
    </p>
    <ul>
      <li v-for="item in movieList" style="list-style: none; margin-top: 40px; padding: 10px">
        <div><a :href="item.url" target="_blank" class="title_css">{{ item.title }}</a></div>
        <div class="digest_css">{{item.digest}}</div>
          <div class="source_css">来源:{{item.source}}</div>
          <div class="ReportTime_css">发布时间:{{item.report_time}}</div>
          <div class="clearfloat"></div>
      </li>
    </ul>
  </Card>
</template>

<script>
  import axios from 'axios'
    export default {
      data () {
        return {
          movieList: [
      ],
          randomMovieList: []
        }
      },
      methods: {
        response() {
          let that = this;
          let s_id = that.$route.params.word;
          const path = 'http://148.70.148.229:8003/api/report/';
          axios.get(path, {params: {report_title: s_id}}).then(function (response) {
            let result = response.data.data;
            that.movieList = result
          })
        },
      },
      created() {
        this.response()
      },
    }
</script>

<style scoped>
  .title_css {
    font-family: PingFangSC-Medium;
    font-size: 32px;
    font-weight: bold;
    color: #333333;
    letter-spacing: 0;
}
  .digest_css{
    font-family: PingFangSC-Regular;
    font-size: 28px;
    color: #333333;
    letter-spacing: 0;
    line-height: 42px;
  }
  .source_css{
    font-family: PingFangSC-Regular;
    font-size: 24px;
    color: #999999;
    letter-spacing: 0;
    float: left;
  }
  .ReportTime_css {
    font-family: PingFangSC-Regular;
    font-size: 24px;
    color: #999999;
    letter-spacing: 0;
    float: right;
  }
  .clearfloat{
    clear:both
  }
  .report_title{
    font-family: PingFangSC-Medium;
    font-size: 36px;
    color: #FF3B50;
    text-align: center;
  }
</style>



