import Vue from 'vue'
import Router from 'vue-router'
import Test from "../components/Test";
import Write_page from "../components/Write_page";
import try_paper from "../components/try_paper";
import Report_info from "../components/Report_info";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/YQ',
      name: '主页',
      component: Test
    },
    {
      path: '/YQ/report_list',
      name: '周报列表',
      component: Write_page
    },
    {
      path: '/YQ/report/:word',
      name: '周报操作',
      component: try_paper
    },
    {
      path: '/YQ/export/:word',
      name: '聚合',
      component: Report_info
    },

  ]

})



