<template>
  <div style="width: 600px;margin: 0 auto">
    <h1 style="text-align: center">舆情报告</h1>
    <Modal v-model="warning" scrollable title="删除提醒" @on-ok="ok" @on-cancel="cancel">
      <div>是否删除周报</div>
    </Modal>
    <Table border stripe :columns="report_columns" :data="report_list"></Table>
  </div>
</template>

<script>
  import axios from 'axios'
    export default {
      data (){
        return{
          search_model : '',
          warning :false,
          report_columns : [
            {
              title: '报告列表',
              align:'center',
              key:'collect_time'
            },
            {
              title: '操作',
              align:'center',
              render: (h, params) => {
                return h('div', [
                  h('Icon', {
                    props: {
                      type: 'md-trash',
                      size: 30,
                    },
                    style: {
                      marginRight: '30px'
                    },
                    on: {
                      click: () => {
                        this.remove(params.index)
                      }
                    }
                  },),
                  h('Icon', {
                    props: {
                      type: 'md-clipboard',
                      size: 30,
                    },
                    style: {
                      marginLeft: '30px'
                    },
                    on: {
                      click: () => {
                        this.jump_info(params.index)
                      }
                    }
                  },),
                ]);
              }

            }
          ],
          report_list: [],
          click_num : ''
        }
      },
      methods: {
        remove (index) {
          this.warning = true;
          this.click_num = index
        },
        jump_info (index) {
          let that = this;
          let report_title = that.report_list[index].collect_time;
          let details = this.$router.resolve({path:"report/" + report_title});
          window.open(details.href, '_blank')
        },
        ok () {
          this.$Message.info('已删除');
          let that = this;
          let report_title = that.report_list[this.click_num].collect_time;
          const path = 'http://148.70.148.229:8003/api/delete_report/';
          axios.get(path,{params:{collect_time:report_title}}).then(function (response) {
          });
          this.$router.go(0)
        },
        cancel () {
          this.$Message.info('取消了删除')
        },
        get_list () {
          let that = this;
          const path = 'http://148.70.148.229:8003/api/report_list/';
          axios.get(path).then(function (response) {
            let msg = response.data.report_list;
            that.report_list = msg
          })
        },
      },
      created() {
        this.get_list()
      }
    }
</script>

<style scoped>

</style>



