<template>
  <div class="layout">
    <Layout>
      <Header>
      </Header>
      <Content :style="{padding: '0 50px'}">
        <Card>
          <div style="min-height: 800px;">
            <h1 style="text-align: center">{{this.$route.params.word}}</h1>
            <Button style="text-align: right" type="primary" size="large" @click="exportData"><Icon type="ios-download-outline"></Icon> 分享页面</Button>
            <Button style="text-align: right" type="primary" size="large" @click="addData"><Icon type="heart"></Icon>新增内容</Button>
            <div class="Table_data" style="margin-top: 20px">
              <Table border stripe :columns="report_result" :data="result_list" ref="table">
                <template slot-scope="{ row, index }" slot="id">
                  <Input type="text" v-model="editID" v-if="editIndex === index" />
                  <span v-else>{{ row.id }}</span>
                </template>
                <template slot-scope="{ row, index }" slot="title">
                  <Input type="text" v-model="editTitle" v-if="editIndex === index" />
                  <span v-else>{{ row.title }}</span>
                </template>
                <template slot-scope="{ row, index }" slot="digest">
                  <Input type="text" v-model="editDigest" v-if="editIndex === index" />
                  <span v-else>{{ row.digest }}</span>
                </template>
                <template slot-scope="{ row, index }" slot="source">
                  <Input type="text" v-model="editSource" v-if="editIndex === index" />
                  <span v-else>{{ row.source }}</span>
                </template>
                <template slot-scope="{ row, index }" slot="report_time">
                  <Input type="text" v-model="editReportTime" v-if="editIndex === index" />
                  <span v-else>{{ row.report_time }}</span>
                </template>
                <template slot-scope="{ row, index }" slot="url">
                  <Input type="text" v-model="editUrl" v-if="editIndex === index" />
                  <span v-else>{{ row.url }}</span>
                </template>
                <template slot-scope="{ row, index }" slot="action">
                  <div v-if="editIndex === index">
                    <Button @click="handleSave(index)">保存</Button>
                    <Button @click="editIndex = -1">取消</Button>
                  </div>
                  <div v-else>
                    <Button @click="handleEdit(row, index)">操作</Button>
                    <Button @click="handleDelete(index)">删除</Button>
                  </div>
                </template>
              </Table>
            </div>
          </div>
        </Card>
      </Content>
      <Footer class="layout-footer-center">BIG_DATA 舆情搜索</Footer>
    </Layout>
  </div>
</template>
<script>
  import axios from 'axios'
    export default {
      data() {
          return {
            report_result: [
              {
                title: '编号',
                slot: 'id',
                width:70
              },
              {
                title: '标题',
                slot: 'title',
                align: 'center',
              },
              {
                title: '概要',
                slot: 'digest',
                align: 'center',
              },
              {
                title: '来源',
                slot: 'source',
                width: 100,
                align: 'center',
              },
              {
                title: '发表时间',
                slot: 'report_time',
                width:100,
                align: 'center',
              },
              {
                title: '文章链接',
                slot: 'url',
                width:150,
                align: 'center',
              },
              {
                title: '操作',
                slot: 'action',
                width: 180,
                align: 'center',
              }
            ],
            result_list: [],
            editIndex: -1,  // 当前聚焦的输入框的行数
            editID:'',
            editTitle: '',  // 第一列输入框，当然聚焦的输入框的输入内容，与 data 分离避免重构的闪烁
            editDigest: '',  // 第二列输入框
            editSource: '',
            editReportTime:'',
            editUrl:''
          }
        },
      methods: {
        response() {
          let that = this;
          let s_id = that.$route.params.word;
          const path = 'http://148.70.148.229:8003/api/report/';
          axios.get(path, {params: {report_title: s_id}}).then(function (response) {
            let result = response.data.data;
            that.result_list = result
          })
        },
        exportData () {
          let keyword = this.$route.params.word;
          let url = 'http://148.70.148.229/#/YQ/export/' + keyword;
          window.open(url, '_blank')
        },
        handleEdit (row, index) {
          this.editID = row.id;
          this.editTitle = row.title;
          this.editDigest = row.digest;
          this.editSource = row.source;
          this.editReportTime = row.report_time;
          this.editUrl = row.url;
          this.editIndex = index;
        },
        handleSave (index) {
          this.result_list[index].id = this.editID;
          this.result_list[index].title = this.editTitle;
          this.result_list[index].digest = this.editDigest;
          this.result_list[index].source = this.editSource;
          this.result_list[index].report_time = this.editReportTime;
          this.result_list[index].url = this.editUrl;
          this.editIndex = -1;
          this.update_data();
        },
        update_data() {
          const path = 'http://148.70.148.229:8003/api/edit/';
          axios.get(path, {params: {id:this.editID,title:this.editTitle,digest:this.editDigest,report_time:this.editReportTime,source:this.editSource,url:this.editUrl}}).then(function (response) {
          })
        },
        handleDelete(index) {
          const path = 'http://148.70.148.229:8003/api/delete/';
          axios.get(path, {params: {re_id:this.result_list[index].id}}).then(function (response) {
          });
          this.$router.go(0)
        },
        addData() {
          const path = 'http://148.70.148.229:8003/api/add/';
          axios.get(path, {params: {collect_time:this.$route.params.word}}).then(function (response) {
          });
          this.$router.go(0)
        }
      },
      created() {
          this.response()
      }
    }
</script>

<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }

  .layout-footer-center{
    text-align: center;
  }
</style>



